:root {
    /* Colors */

    --color_crimson: #a70418;
    --color_crimson_transparent: rgba(167, 4, 24, 0.9);

    --color_midnight: #040405;

    --color_raven: #1c1f25;

    --color_charcoal: #282C34;
    --color_charcoal_transparent: rgba(40, 44, 52, 0.9);

    --color_white: #ffffff;
    --color_alabaster: #eeeded;

    /* Spacing */

    --spacing_small: 8px;
    --spacing_medium: 16px;
    --spacing_large: 24px;
    --spacing_huge: 32px;

    /* Padding */

    --padding_small: var(--spacing_small);
    --padding_medium: var(--spacing_medium);
    --padding_large: var(--spacing_large);
    --padding_huge: var(--spacing_huge);

    /* Margin */

    --margin_small: var(--spacing_small);
    --margin_medium: var(--spacing_medium);
    --margin_large: var(--spacing_large);
    --margin_huge: var(--spacing_huge);
}

h1 {
    color: var(--color_alabaster);
    font-size: larger;
    font-weight: bold;
    margin: 0;
}

h2 {
    color: var(--color_alabaster);
    font-size: large;
    font-weight: bold;
    margin: 0;
}

h3 {
    color: var(--color_alabaster);
    font-size: medium;
    font-weight: bold;
    margin: 0;
}

p {
    color: var(--color_alabaster);
    font-size: medium;
    margin: 0;
}

a {
    color: var(--color_alabaster);
    text-decoration: none;
    margin: 0;
}

Link {
    color: var(--color_alabaster);
    text-decoration: none;
    margin: 0;
}

a:hover {
    color: var(--color_white);
    text-decoration: underline;
}

.visible_link {
    text-decoration: underline;
}

input {
    -webkit-appearance: none;
    -moz-appearance: textfield;
    width: 100%;
    box-sizing: border-box;
    padding: var(--padding_small);
    background-color: var(--color_raven);
    color: var(--color_alabaster);
    font-family: "Inria Sans";
    outline: 0;
    border-width: 0 0 2px;
    border-color: var(--color_crimson);
}

label {
    box-sizing: border-box;
    color: var(--color_alabaster);
    font-size: medium;
}

@media only screen and (max-width: 769px) {
    label {
        font-size: small;
    }
}